@import '../mixins/mixins';

.footer {
    min-height: 15vh;
    bottom: 0;
    background-color: #191919;
    color: #7e7e7e;
    display: flex;
    flex-direction: column;
    justify-content: center;
    .footer-copy {
        color: grey;
        font-family: monospace;
    }
    & a {
        color: white;
    }
    .social-media ul li a {
        color: white;
        &:hover {
            color: #ffdbbb;
        }
    }
    @include tablet {
        font-size: 14px;
    }
}
